body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header{background:#aed1ce;color:#ececec;width:100%;position:fixed;display:flex;flex-direction:column;justify-content:center;align-items:center;top:0;z-index:1000}.title-icon-container{padding:1rem 0}.heading-title{display:inline;font-family:"Roboto Condensed",sans-serif;font-weight:400;font-size:4rem}.cv-icon{font-size:3.5rem;margin-left:1rem;color:#e29578;padding-bottom:.3rem}.header-buttons{width:100%;padding-right:1rem;display:flex;justify-content:flex-end;background:#e29578}.header-buttons span{cursor:pointer;transition:.3s ease-in-out}.header-buttons span:hover{opacity:.6;transform:scale(1.1)}.buttons{font-size:3rem;display:flex;bottom:-10rem;right:3rem;color:#aed1ce}.buttons span{margin:.5rem}.buttons .print-button{left:-1rem}.eye-icon{font-size:2rem}.show-bottom-border{border-bottom:2rem #aed1ce solid}



.display-none{display:none}.button-container{width:100%;border:solid}.profile-photo{width:40%;max-width:150px;border-radius:5px}.general-info{margin-top:5rem !important}@media(min-width: 750px){.general-info{margin-top:8rem !important}}
.add-button{background:none;border:none;color:#ececec;font-size:2rem;position:absolute;right:1rem;top:1rem}

.cv-header{width:100%;background:#aed1ce;color:#fff;height:150px;display:flex;justify-content:space-between;align-items:center;padding:2rem;margin-bottom:2rem}.info-icon{margin-right:.5rem}.img-name-container{display:flex;align-items:center;text-align:center}.img-name-container .name{font-size:2.5rem;margin-left:1rem}.img-name-container img{-webkit-filter:sepia(0.1);filter:sepia(0.1);opacity:.9}.header-profile-photo{width:100px;height:100px;object-fit:cover;border-radius:50%}.general-info-container{text-align:right}
.cv-preview-container{width:595px;background:#fff;margin:0 auto;margin-top:8rem;min-height:842px;font-size:1.3rem;color:#333;box-shadow:0 14px 28px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.22);margin-bottom:5rem}@media screen and (max-width: 650px){.cv-preview-container{zoom:.8}}@media screen and (max-width: 500px){.cv-preview-container{zoom:.7}}@media screen and (max-width: 450px){.cv-preview-container{zoom:.6}}@media screen and (max-width: 375px){.cv-preview-container{zoom:.5}}

.education-segment{display:grid;width:100%;grid-template-columns:repeat(3, 1fr);align-items:center;margin-bottom:2rem}
.experience-segment{display:grid;grid-template-columns:2fr 3fr 1fr;grid-gap:1.5rem;padding-bottom:3rem}
html{box-sizing:border-box;font-size:10px}*,*:before,*:after{box-sizing:inherit}body,h1,h2,h3,h4,h5,h6,p,ol,ul{margin:0;padding:0;font-weight:normal}ol,ul{list-style:none}img{max-width:100%;height:auto}
body{font-family:"Cabin",sans-serif;font-weight:400;background:#4e4e4e;padding-top:8rem}input:focus,button:focus,textarea:focus{outline:none;box-shadow:0 0 0px 3px #aed1ce}input,textarea{font-size:1.5rem;font-family:"Cabin",sans-serif}input{height:3.5rem;border-radius:.5rem;border:none;padding-left:1rem;max-width:600px}.line{margin:2rem;border:1px #aed1ce solid;opacity:.5}.input-full-width{width:100%;margin:1rem 0}.text-area{width:100%;border:none;border-radius:.5rem;padding:1rem;font-family:"Cabin",sans-serif;max-width:600px}.grid{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:1rem;max-width:600px;margin:0 auto}@media only screen and (max-width: 600px){.grid{grid-template-columns:1fr}}.section{background:gray;margin:0 auto;margin-bottom:2.5rem;text-align:center;color:#fff;font-family:"Roboto Condensed",sans-serif;border-radius:.2rem;padding:1rem;position:relative;width:80%;max-width:700px;min-width:300px;box-shadow:0 14px 28px rgba(0,0,0,.25),0 10px 10px rgba(0,0,0,.22)}.title{font-size:3rem;font-family:"Roboto Condensed",sans-serif;margin-bottom:1rem}.input-button{display:inline-block;background:#ececec;padding:1rem;color:rgba(0,0,0,.541);border-radius:5px;font-size:1.4rem;width:100%;margin:1rem 0;max-width:600px;border:none;cursor:pointer}.input-button:hover{opacity:.8}.cv-segment{margin:5rem 4rem}.cv-segment i{color:#aed1ce;margin-right:1rem}.cv-segment hr{background-color:#aed1ce;height:1px;border:0;margin-bottom:2rem}.center{text-align:center}.right{text-align:right}.green{color:#065}.add-button{background:none;border:none;color:#ececec;font-size:2rem;position:absolute;right:1rem;top:1.7rem;cursor:pointer}.add-button:hover{opacity:.5}.empty-message{font-size:1.5rem;margin-top:1rem;opacity:.6}@media print{.no-print{display:none}body *{visibility:hidden;-webkit-print-color-adjust:exact}html{font-size:13px}p{page-break-inside:avoid;-webkit-column-break-inside:avoid;break-inside:avoid}.cv-preview-container{zoom:1;width:585px;padding:0;margin:0;width:100%;position:absolute;top:0;left:0;box-shadow:none}.no-break{page-break-inside:avoid;-webkit-column-break-inside:avoid;break-inside:avoid}.cv-preview-container,.cv-preview-container *{visibility:visible}@page{size:A4;margin:2rem 2rem 0 2rem;padding-bottom:2rem;padding-top:3rem}}@media(max-width: 500px){body{padding-top:10rem}}
